import {Component, OnInit} from '@angular/core';
import { ROUTER_DIRECTIVES } from '@angular/router';
@Component({
    moduleId:module.id,
    selector: 'admin-ingatlan-header',
  directives:[ROUTER_DIRECTIVES],
    template: `
<div class="container-fluid ">
        <div class="row">
        <header >
            <a href="#/admin/ingatlanok"><div class="col-md-6 headCol">
                <div class="head-labels"  [ngStyle]="col_1style"> Ingatlanok listája</div>
            </div></a>
             <a [routerLink]="['/ingatlanok/modify','new']"><div class="col-md-6 headCol">
                <div class="head-labels" [ngStyle]="col_2style">Új ingatlan felvétele</div>
            </div></a>
                </header>
    </div>
  <router-outlet></router-outlet>
</div>
    `,
    styleUrls: ["admin.css"],

})

export class AdminIngatlanHeaderComponent implements OnInit {
    col_1style:any;
    col_2style:any;
    ngOnInit() {
        var links = window.location.href.split("/");
        let url = links[links.length - 1];
        switch (url) {
            case 'ingatlanok':
                this.col_1style ={'background-color':'#000066'};
                break;
            case 'new':
                this.col_2style ={'background-color':'#000066'};
            break;

            default:
                this.col_1style ={'background-color':'#000066'};
            break;
        }


    }


}
